<template>
    <div class="area">
        <h1>
            Upload  
            <span class="fileType" v-if="isTextBook"><u>Textbook</u></span>
            <span class="fileType" v-if="!isTextBook"><u>Journals</u></span>
            File
        <label class="switch" title="Click here to change file type">
            <input v-model="isTextBook" type="checkbox">
            <span class="slider"></span>
        </label>
        </h1>
        <hr>
        <form action="#" method="post" name="uploadForm">
            Upload File : <input type="file" ref="file" v-on:change="handleUploadedFile()">
            <button class="sBtn" v-if="!loading" @click="submitUploadForm" type="button">Upload</button>
        </form>

        <img v-if="loading" src="../assets/loading.gif" alt="loading..">
        <div v-if="!loading">
            <h2 class="success" v-if="success">{{success}}</h2>
            <h2 class="error" v-if="error">{{error}}</h2>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:"UploadComponent",
    data () {
        return {
            file:'',
            loading:false,
            isTextBook:true,
            success:'',
            error:'',
        }
    },
    methods: {
        handleUploadedFile()
        {
            this.file = this.$refs.file.files[0];
        },
        submitUploadForm ()
        {
            if(this.file)
            {
                this.loading = true;
                this.success = '';
                this.error = '';

                console.log('file selected and clicked for upload');
                let formData = new FormData();
                formData.append('myFile',this.file);
                let url = "";
                if(this.isTextBook)
                {
                    url = "data-import/text-books";
                }else {
                    url = "data-import/journal-books";
                }

                axios.post('http://acec5a1b8f52.ngrok.io/'+url, formData)
                .then(response => {
                    this.loading = false;
                    console.log(response);
                    this.showNotification(response.data,"success");
                })
                .catch(error => {
                    this.loading = false;
                    console.log(error);
                    console.log(error.response.data);
                    this.showNotification(error.response.data, "error");
                })
                
            }else {
                this.showNotification("Please select a file . ","error");
            }
        },
        showNotification(notification, type)
        {
            if(type == "success")
            {
                this.success = notification;
            }else {
                this.error = notification;
            }
        }
    }
}
</script>

<style scoped>
    body{
        font-family:fantasy;
    }
    .area {
        width: 50%;
        margin: 0px auto;
        background: #ffffff;
        border: 4px #ddd solid;
        padding: 5%;
        text-align: center;
    }
    .sBtn {
        width: 112px;
        height: 39px;
        background: auto;
        border-radius: 3px;
    }

    .fileType {
        color: green;
    }
    .success {
        color:green;
    }
    .error {
        color:red;
    }


    /** Toggle btn  */

    .switch {
        position: relative;
        display: inline-block;
        width: 60px;
        height: 34px;
        }

        .switch input { 
        opacity: 0;
        width: 0;
        height: 0;
        }

        .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        -webkit-transition: .4s;
        transition: .4s;
        }

        .slider:before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
        }

        input:checked + .slider {
        background-color: #2196F3;
        }

        input:focus + .slider {
        box-shadow: 0 0 1px #2196F3;
        }

        input:checked + .slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
        }

        /* Rounded sliders */
        .slider.round {
        border-radius: 34px;
        }

        .slider.round:before {
        border-radius: 50%;
        }
</style>